<template lang="pug">
#project 
  .content-container(
    :style="{ opacity: showEl ? 1 : 0, transform: showEl ? 'translateY(0px)' : 'translateY(-20px)' }"
  )
    .heading 
      .heading-row
        p Projects
        .heading-row-line.small-up
      .heading-column
        .heading-column-line.small-up
        .inner-content
          .project-type-selection
            .personal(
              :class="{ 'selected-tab': projectType === 'personal' }",
              @click="selectType('personal')"
            )
              p(:class="{ 'selected-tab-text': projectType === 'personal' }") Personal
            .work(
              :class="{ 'selected-tab': projectType === 'work' }",
              @click="selectType('work')"
            )
              p(:class="{ 'selected-tab-text': projectType === 'work' }") Work
          .projects-container
            .project-el(
              v-for="(project, index) in projectsByType",
              :key="'project-' + index",
              @click="selectProject(project)"
            )
              .project-overlay
                .project-name
                  p {{ project.projectName }}
                .project-cta
                  p LEARN MORE
              .project-img(
                :style="{ background: project.coverBgColor ? project.coverBgColor : '#fff' }"
              )
                img(:src="project.coverImage")
              //- .project-img
</template>

<script>
export default {
  data() {
    return {
      showEl: false,
      workProjects: [
        "https://family.ikea.com.sg/",
        "https://family.ikea.com.my/",
        "https://family.ikea.co.th/",
        "https://worldmadebetter.kiehls.com.my/join-us-now/",
        "https://brickmagic.asia/",
      ],
      personalProjects: [
        "https://codenames-the-game.web.app/",
        "https://codenames-the-game.web.app/",
        "https://codenames-the-game.web.app/",
      ],
      projectType: "personal",
    };
  },
  computed: {
    projectsByType() {
      if (this.projectType === "work") {
        return this.workProjects;
      } else {
        return this.personalProjects;
      }
    },
  },
  methods: {
    selectProject(project) {
      this.$store.commit("setSelectedProject", project);
    },
    selectType(type) {
      this.projectType = type;
    },
    initPersonalProjects() {
      this.personalProjects = [
        {
          coverImage: require("../assets/projects/nby.png"),
          projectName: "ProtoWire [WIP]",
          gif: "https://via.placeholder.com/300x250",
          description: `An online wireframing tool designed to make sketching wireframes easier by having an
            intuitive and easy-to-use UI.`,
          motivation: `I got the idea when I was trying to design other projects and realised how unintuitive 
            online wireframing tools are. My initial idea was to make a simple shape drawing tool to make simple wireframe sketches. However, I soon
            realised there were more and more features I wanted to add - which caused this project to balloon. I now work on this project every now and then as a
            hobby due to its large size.`,
          links: ["https://codenames-the-game.web.app/"],
          linkLabels: [""],
          techStack: ["VueJs", "Firebase Hosting"],
        },

        {
          coverImage: require("../assets/projects/codenames.jpg"),
          gif: require("../assets/codenames.gif"),
          projectName: "CodeNames",
          description: `A realtime multiplayer online version of the boardgame Codenames.`,
          motivation: `Prior to the Covid-19 Lockdown in Malaysia, my friends and I would frequently gather at Boardgame Cafes
            to play a bunch of games together. When the Movement-Control Order got announced, we found ourselves really bored - so I decided to take it
            upon myself to re-create the CodeNames Boardgame. Not only did I get to have fun with my friends, I learnt
            more about Firebase and its Realtime Database. `,
          links: ["https://codenames-the-game.web.app/"],
          linkLabels: [""],
          techStack: [
            "VueJs",
            "Firebase Hosting",
            "Cloud Functions",
            "Realtime Database",
          ],
        },
        //     {
        //       coverImage: require("../assets/projects/kiehls-wmb.png"),
        //       projectName: "Toggl Timesheet",
        //       gif: "https://via.placeholder.com/300x250",
        //       description: `A timesheet tracking tool that makes it easy for employees to submit their timesheet. Using the report
        //         feature of a time tracking tool called Toggl Track, one can simply upload their report to the website,
        //         and the weekly time reports will be automatically filled in.`,
        //       motivation: `I frequently use a time tracking tool known as Toggl Track. It tracks the time I spend on various tasks
        //  and provides significant insight on how I can improve my productivity. In one of my previous companies, I noticed people had a lot of complaints about the way they submit their
        //  timesheet. So I decided to put 2 and 2 together to create this app`,
        //       links: ["https://codenames-the-game.web.app/"],
        //       linkLabels: [""],
        //       techStack: ["VueJs"],
        //     },
      ];
    },
    initWorkProjects() {
      this.workProjects = [
        {
          coverImage: require("../assets/projects/kiehls-wmb.png"),
          coverBgColor:
            "linear-gradient(180deg,hsla(0,0%,100%,0) 68.75%,hsla(0,0%,100%,.568392)),#42cbef",
          projectName: "Kiehls World Made Better",
          gif: require("../assets/kiehls-fmb.gif"),
          description: `A microsite developed to promote Kiehl's World Made 
            Better campaign which aims to increase awareness towards environmental 
            impact, sustainable living, and beauty. <br><br> The microsite involves a virtual 
            world with immersive and interactive features designed to give the user a 
            sense of making impactful changes by performing one's own duty.`,
          responsibilities: [
            "I was one of the main developer in a small team of 3 developers total that built the entire microsite from scratch.",
          ],
          links: ["https://worldmadebetter.kiehls.com.my/join-us-now/"],
          linkLabels: [""],
          techStack: ["NuxtJs", "Typescript", "ExpressJs", "MySQL"],
        },
        {
          coverImage: require("../assets/projects/brickmagic.png"),
          coverBgColor: "#ffd500",
          projectName: "Brickmagic",
          gif: require("../assets/brickmagic.gif"),
          description: `An e-commerce site for LEGO that allows customers to shop for LEGO products online. 
            It also supports a lego membership system known as VIB Membership.`,
          responsibilities: [
            "Maintained and developed additional features for the admin panel of the site.",
          ],
          links: ["https://brickmagic.asia/"],
          linkLabels: [""],
          techStack: ["VueJs", "Typescript", "ExpressJs", "MySQL"],
        },
        {
          coverImage: require("../assets/projects/nby.png"),
          projectName: "NikeByYou",
          gif: require("../assets/nby-mov.gif"),
          description: `A microsite that allows users to customize their
            own Nike products. <br><br> <span style="color: red; font-style: italic;"> The microsite is only allowed to be used in specific 
            Nike Stores and cannot be accessed by the general public.</span>`,
          responsibilities: [
            "I was the lead/solo developer for all NikeByYou microsites across multiple countries. <br><br>",
            "I also had to manage the NikeByYou projects and was involved in all phases of the software development lifecycle - including requirements/timeline planning.<br><br>",
            "Among the countries include: Singapore, Thailand, and Phillipines",
          ],
          links: [],
          linkLabels: [],
          techStack: [
            "VueJs",
            "Firebase Hosting",
            "Cloud Functions",
            "Firestore",
          ],
        },
        {
          coverImage: require("../assets/projects/ikea-family.png"),
          projectName: "IKEA Family",
          gif: require("../assets/ikea-family.gif"),
          description: `The purpose of the IKEA Family websites is to provide 
            exclusive offers, activities, information, etc. to IKEA Family members. <br><br> On this site,
            members are able to view special discounts on certain items, as well as 
            access information about their Family membership.`,
          responsibilities: [
            "Improved and optimised various functionalities of the IKEA Family websites",
          ],
          links: [
            "https://family.ikea.com.sg/",
            "https://family.ikea.com.my/",
            "https://family.ikea.co.th/",
          ],
          linkLabels: ["Singapore", "Malaysia", "Thailand"],
          techStack: ["VueJs", "Laravel", "MySQL", "PostgreSQL"],
        },
      ];
    },
    initIntersectionObserver() {
      // eslint-disable-next-line
      const observer = new IntersectionObserver(
        (entries) => {
          if (entries[0].intersectionRatio > 0) {
            if (!this.showEl) {
              this.showEl = true;
            }
            // console.log("Cross Project");
          }
        },
        {
          // rootMargin: "0px 0px -200px 0px",
          threshold: 0.5,
        }
      );
      observer.observe(document.querySelector("#project"));
      this.$store.state.intersectionObserver.observe(
        document.querySelector("#project")
      );
    },
  },
  mounted() {
    this.initPersonalProjects();
    this.initWorkProjects();
    setTimeout(() => {
      this.initIntersectionObserver();
    }, 2000);
  },
};
</script>

<style lang="sass" scoped>
@import '../assets/sass/variables.sass'
#project
  width: 100%
  padding: 100px 0px
  position: relative
  +mobile
    padding: 50px 0px

.content-container
  position: relative
  width: 100%
  max-width: 1024px
  margin: auto
  transition: all 1s ease

.project-type-selection
  display: flex
  justify-content: center
  margin-bottom: 20px
  .work
    box-sizing: border-box
    cursor: pointer
    p
      font-size: 20px
      +mobile
        font-size: 18px
  .personal
    margin-right: 50px
    cursor: pointer
    box-sizing: border-box
    p
      font-size: 20px
      +mobile
        font-size: 18px
  .work:hover
    // border-bottom: 5px solid #c7e4e9
    border-bottom: 3px solid #30DEFF
    p
      color: #30DEFF
  .personal:hover
    // border-bottom: 5px solid #c7e4e9
    border-bottom: 3px solid #30DEFF
    p
      color: #30DEFF

.selected-tab
  border-bottom: 3px solid #30DEFF
.selected-tab-text
  color: #30DEFF

.projects-container
  display: flex
  flex-wrap: wrap
  justify-content: center
  width: 100%
  +mobile
    flex-direction: column

.project-el
  width: 300px
  flex-grow: 0
  flex-shrink: 0
  height: 250px
  // border: 1px solid #fff
  border-radius: 10px
  // margin: 0px 10px
  margin: 10px
  overflow: hidden
  cursor: pointer
  transition: all 1s ease
  position: relative
  box-shadow: 0px 0px 5px 2px #000
  +mobile
    margin: 10px auto

  .project-overlay
    background-color: rgba(28, 48, 52, 0.95)
    width: 100%
    height: 100%
    position: absolute
    top: 0%
    opacity: 0
    transition: all 1s ease
    display: flex
    flex-direction: column
    align-items: center
    justify-content: center
    .project-name
      text-align: center
      p
        font-size: 30px
        font-weight: bold
        color: #c7e4e9
    .project-cta
      p
        font-weight: bold
  .project-img
    // background: yellow
    width: 100%
    height: 100%
    display: flex
    justify-content: center
    align-items: center
    img
      max-width: 100%
      max-height: 100%
  .project-cta
    margin-top: 20px
    border: 3px solid #30DEFF
    padding: 10px 20px
    display: flex
    align-items: center
    justify-content: center
    p
      color: #c7e4e9
      // color: #fff

.project-el:hover
  box-shadow: 0px 0px 5px 2px #30DEFF
  .project-overlay
    opacity: 1

.heading
  text-align: left
  display: flex
  align-items: flex-start
  flex-direction: column
  .heading-row
    display: flex
    align-items: center
    +mobile
      border-bottom: 1px solid #30DEFF
      width: 100%
    p
      color: #30DEFF
      font-size: 42px
      font-weight: bold
      +mobile
        font-size: 30px
    .heading-row-line
      width: 400px
      height: 1px
      flex-grow: 1
      background-color: #30DEFF
      margin-left: 20px
  .heading-column
    display: flex
    align-items: flex-start
    flex-grow: 1
    width: 100%
    .heading-column-line
      width: 1px
      flex-grow: 1
      background-color: #30DEFF
      margin-top: 10px
      margin-left: 20px
      height: 400px
    .inner-content
      width: 100%
      padding: 20px 20px 0px 100px
      +mobile
        padding: 20px 0px 0px
        box-sizing: border-box
</style>
